.center {
  position: absolute;
  top: 50%;
  left: 50%;
}

.card-cc {
  width: 100%;
  height: 200px;
  background-color: #fff;
  background: -webkit-gradient(linear, left top, left bottom, from(#f8f8f8), to(#fff));
  background: linear-gradient(#f8f8f8, #fff);
  box-shadow: 0 8px 16px -8px rgba(0,0,0,0.4);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
  margin: 1.5rem;
}

.card-cc h1 {
  text-align: center;
}

.card-cc .additional {
  position: absolute;
  width: 150px;
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#dE685E), to(#EE786E));
  background: linear-gradient(#dE685E, #EE786E);
  -webkit-transition: width 0.4s;
  transition: width 0.4s;
  overflow: hidden;
  z-index: 2;
}

.card-cc.green .additional {
  background: #1FFCEF;
}


.card-cc .additional {
  width: 100%;
  border-radius: 0 5px 5px 0;
}

.card-cc .additional .user-card-cc {
  width: 150px;
  height: 100%;
  position: relative;
  float: left;
}

.card-cc .additional .user-card-cc::after {
  content: "";
  display: block;
  position: absolute;
  top: 10%;
  right: -2px;
  height: 80%;
  border-left: 2px solid rgba(0,0,0,0.025);
}

.card-cc .additional .user-card-cc .level,
.card-cc .additional .user-card-cc .points {
  top: 15%;
  color: #fff;
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: bold;
  background: rgba(0,0,0,0.15);
  padding: 0.125rem 0.75rem;
  border-radius: 100px;
  white-space: nowrap;
}

.card-cc .additional .user-card-cc .points {
  top: 85%;
}

.card-cc .additional .user-card-cc svg {
  top: 50%;
}

.card-cc .additional .more-info {
  width: 300px;
  float: left;
  position: absolute;
  left: 150px;
  height: 100%;
}

.card-cc .additional .more-info h1 {
  color: #fff;
  margin-bottom: 0;
}

.card-cc.green .additional .more-info h1 {
  color: #224C36;
}

.card-cc .additional .coords {
  margin: 0 1rem;
  color: #fff;
  font-size: 1rem;
}

.card-cc.green .additional .coords {
  color: #325C46;
}

.card-cc .additional .coords span + span {
  float: right;
}

.card-cc .additional .stats {
  font-size: 2rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  right: 1rem;
  top: auto;
  color: #fff;
}

.card-cc.green .additional .stats {
  color: #325C46;
}

.card-cc .additional .stats > div {
  flex: 1 1;
  text-align: center;
}

.card-cc .additional .stats i {
  display: block;
}

.card-cc .additional .stats div.title {
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
}

.card-cc .additional .stats div.value {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.5rem;
}

.card-cc .additional .stats div.value.infinity {
  font-size: 2.5rem;
}

.card-cc .general {
  width: 70%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  box-sizing: border-box;
  padding: 1rem;
  padding-top: 0;
}

.image-child {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 50%;
}

.card-cc .general .more {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  font-size: 0.9em;
}

