#header {
    position: absolute;
    width: 220px;
  }
  .admin-navbar-nav > .pro-sidebar > .pro-sidebar-inner {
    background-color: #0e1e24 !important
  }
  .teacher-navbar-nav > .pro-sidebar > .pro-sidebar-inner {
    background-color: #503E9D !important
  }
  .teacher-navbar-nav  .pro-sidebar  .pro-sidebar-inner  .pro-icon-wrapper {
    background-color: #503E9D !important
  }

  #teacher_navbar {
    border-top-left-radius: 50px !important;
    border-top-right-radius: 50px !important;
  }

  .teacher-navbar-nav .pro-sidebar .pro-menu > ul > .pro-sub-menu > .pro-inner-list-item {
    background-color: #7659f7 !important;
  } 

  .img-logo {
    width: 100%;
}

  .logo-x {
    padding-top: 7px;
  }
  #collapseMenu .pro-sidebar {
    min-height: 100vh
  }
  .pro-sidebar-header {
    padding: 12px;
  }
  #collapseMenu .closemenu {
    color: #ffffff;
    position: absolute;
    right: 0;
    z-index: 9999;
    line-height: 20px;
    border-radius: 50%;
    font-weight: bold;
    font-size: 22px;
    top: 55px;
    cursor: pointer;
  }
  #header .pro-sidebar {
    width: 100%;
    min-width: 100%;
  }
  #collapseMenu .pro-sidebar.collapsed {
    width: 80px;
    min-width: 80px;
  }
  

  .img-sideleft {
    width: 200px;
    min-width: 200px;
  }
  .collapsed .pro-sidebar-header {
    padding: 12px;
  }
  .collapsed .logo-x {
    left: 0 !important;
  }
  #header .pro-sidebar-inner {
    background-color: white;
    box-shadow: 0.5px 0.866px 2px 0px rgba(0, 0, 0, 0.15);
  }
  #header .pro-sidebar-inner .pro-sidebar-layout {
    overflow-y: hidden;
  }
  #collapseMenu .pro-sidebar-inner .pro-sidebar-layout .logotext p {
    font-size: 20px;
    padding: 0 20px;
    color: #ffffff;
    font-weight: bold;
  }
  #header .pro-sidebar-inner .pro-sidebar-layout ul {
    padding: 0 5px;
  }
  #collapseMenu .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item {
    color: #ffffff !important;
    margin: 10px 0px;
    font-weight: bold;
  }
  #header .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item .pro-icon-wrapper {
    background-color: #fbf4cd;
    color: #000;
    border-radius: 3px;
  }
  #collapseMenu .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item .pro-icon-wrapper .pro-item-content {
    color: #ffffff;
  }
  .pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-header {
    border-bottom: 0px solid rgba(173, 173, 173, 0.2);
}
  #header .pro-sidebar-inner .pro-sidebar-layout .active {
      background-color: #ffffff;
  }


  .pro-inner-item > .pro-item-content {
    color: #ffffff !important;
  }

  .link-active {
    color: #FFBF08 !important;
  }

  .not-active {
    color: #ffffff !important;
  }

  .link-active .pro-icon {
    color: #FFBF08 !important;
  }

  .link-not-active {
    color: #ffffff !important;
  }

  .link-active > .pro-inner-item > .pro-item-content {
    color: #FFBF08 !important;
  }

  .active > .pro-inner-item > .pro-icon-wrapper {
    background-color: #ffffff;
  }

  .pro-menu-item {
    list-style-type: none !important;
  }
  #collapseMenu .logo {
    padding: 20px;
  }
  
  @media only screen and (max-width: 720px) {
    html {
      overflow: hidden;
    }
  }